@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}
body {
  background: #f5f5f5 !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Inter', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
 font-family: 'Inter', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #D90A2C;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #333333;
  margin: 0;
}

.forgot-password a {
  color: #D90A2C;
}
.btn-primary {
    color: #fff;
    background-color: #D90A2C !important;
    border-color: #D90A2C !important;
}
.navbar-light .navbar-brand {
    font-family: 'Inter', sans-serif;
	font-weight: bold;
	font-size: 30px;
}